<template>
  <div id="list">
    <v-back></v-back>
    <!--     Failed to resolve filter: toTime
    局部定义的过滤器 只能在局部有效 如果其他组件内也想拥有该过滤器 你可以重新定义局部过滤器 或者在全局 定义全局过滤器-->
    <h1 id="a">woshi资讯也</h1>
    
    <ul>
      <li v-for="(item,index) in movie" :key="index" @click="goDetail(index)">{{item}}</li>
    </ul>
  </div>
</template>
<script>
//import vBack from './common/back'
export default {
  data() {
    return {
      movie: ["未闻花名", "打上花火", "破风"],
      timeInfo: 1552491770440
    };
  },
  /*     components:{
      vBack
    }, */

  methods: {
    goDetail(id) {
      this.$router.push({
        name: "listDetail",
        params: {
          id: id
        }
      });
    }
  },
  //组件内导航守卫
  // beforeRouteEnter(to, from, next) {
  //   console.log(to, "ttttttttttt");
  //   console.log(from, "fffffffffffff");
  //   console.log(this, "this");
  //   next();
  // },
  // beforeRouteUpdate(to, from, next) {
  //   console.log(to, "UUUUttt");
  //   console.log(from, "UUUUUffff");
  //   console.log(this, "UUUUUUthis");
  // },
  // beforeRouteLeave(to, from, next) {
  //   console.log(to, "Lttt");
  //   console.log(from, "Lffff");
  //   console.log(this, "this");
  //   next();
  // }
};
</script>
<style lang="less" scoped>
#list{
  #a{
    color: brown;
  }
  ul{
    li{
      color: aqua;
    }
  }
}
</style>


